import React, {
  PropsWithChildren, useCallback
} from 'react';
import {
  Box,
  Flex, Image, List, ListItem, Switch, Tag, Text
} from '@chakra-ui/react';

import { shallowEqual } from 'react-redux';
import { NavLink, useNavigate } from 'react-router-dom';
import { useAppSelector } from '../hooks/store';
import style from './Dashboard.module.css';
import Icon from '../components/Icon';
import DrawerProvider from '../context/Drawer';

const DashboardLayout = ({ children }: PropsWithChildren) => {
  const navigate = useNavigate();

  const person = useAppSelector((store) => store.user.personDetails, shallowEqual);
  const logo = useAppSelector((store) => store.core.programConfig?.brand.landscapeLogo, shallowEqual);
  const mode = useAppSelector((store) => store.core.config.mode);

  const navMenuClassName = useCallback(({ isActive }: {isActive: boolean}) => {
    if (isActive) {
      return `${style.sidebarMenuItemLink} ${style.active}`;
    }
    return style.sidebarMenuItemLink;
  }, []);

  return (
    <div className={style.container}>
      <aside className={style.sidebar}>
        <Box width="100%">
          {logo
            ? (
              <Flex padding="0 24px" justifyContent="flex-start" height="68px" alignItems="center" borderBottom="1px solid #ececec">
                <Image maxWidth="254px" src={logo} />
              </Flex>
            ) : null }

          <Flex
            padding="0 24px"
            justifyContent="space-between"
            height="68px"
            alignItems="center"
            borderBottom="1px solid #ececec"
            role="button"
            onClick={() => {
              navigate('/ao/account/list');
            }}
          >
            <Text variant="large" fontWeight="medium">Select account</Text>
            <Icon icon="Arrow-small-right" size={12} className="svg-light-grey-1" />
          </Flex>

          <nav>
            <List mt="15px">
              <ListItem mt={0}>
                <NavLink className={navMenuClassName} to="/dashboard">Overview</NavLink>
              </ListItem>
              <ListItem mt={0}>
                <NavLink className={navMenuClassName} to="/transactions">Transactions</NavLink>
              </ListItem>
              <ListItem mt={0}>
                <NavLink className={navMenuClassName} to="/cards">Cards</NavLink>
              </ListItem>
              <ListItem mt={0}>
                <NavLink className={navMenuClassName} to="/dashboard/transfer-money">Transfer Money</NavLink>
              </ListItem>
              <ListItem mt={0}>
                <NavLink className={navMenuClassName} to="/">Send Money</NavLink>
              </ListItem>
            </List>
          </nav>
        </Box>
        {person
        && (
        <Box padding="12px" borderTop="1px solid #e8ebee">
          <Flex justifyContent="space-between" alignItems="center">
            <Flex alignItems="center">
              <div className={style.initialsInfoLight}><Text variant="regular">{person.firstName.charAt(0) + person.lastName.charAt(0)}</Text></div>
              <Text ml="16px">
                {`${person.firstName} ${person.lastName}`}
              </Text>
            </Flex>
            <Icon icon="Arrow-small-right" size={12} className="svg-light-grey-1" />
          </Flex>
        </Box>
        ) }
      </aside>
      <div className={style.content}>
        <div className={style.nav}>
          <Flex width="100%" justifyContent="flex-end" alignItems="center" height="68px" gap="10px">
            {mode === 'sandbox'
            && (
            <Box padding="0 30px" borderLeft="1px solid #e8ebee">
              <Tag color="#fff" backgroundColor="#ffac4a">TEST Mode</Tag>
            </Box>
            ) }
            <Box paddingLeft="30px" borderLeft="1px solid #e8ebee">
              <Switch variant="primary" />
              <br />
              <Text variant="small">Darkmode</Text>
            </Box>
          </Flex>
        </div>
        <div className={style.contentBody}>
          <DrawerProvider>
            {children}
          </DrawerProvider>
        </div>
      </div>

    </div>
  );
};

export default DashboardLayout;
