import React from 'react';
import { Route, Routes } from 'react-router-dom';
import DashboardLayout from '../../layout/Dashboard';

const CardList = React.lazy(() => import('./CardList/Index'));
const SentCardList = React.lazy(() => import('./SentCardList/SentCardList'));
const CardDetails = React.lazy(() => import('./CardDetails/CardDetails'));
const CardType = React.lazy(() => import('./CardCreation/CardType/CardType'));
const CardInfo = React.lazy(() => import('./CardCreation/CardInfo/CardInfo'));
const CardAddress = React.lazy(() => import('./CardCreation/CardAddress/CardAddress'));
const CardBillingAddress = React.lazy(() => import('./CardCreation/CardBillingAddress/CardBillingAddress'));
const CardCreated = React.lazy(() => import('./CardCreation/CardCreated/CardCreated'));
const EditCardInfo = React.lazy(() => import('./Edit/CardInfo/CardInfo'));
const ATMInfo = React.lazy(() => import('./AtmInfo/AtmInfo'));
const AtmPin = React.lazy(() => import('./AtmPin/AtmPin'));
const AtmList = React.lazy(() => import('./AtmList/AtmList'));
const CardActivation = React.lazy(() => import('./CardActivation/CardActivation'));
const CardActivated = React.lazy(() => import('./CardActivated/CardActivated'));

function AoRouter() {
  return (
    <Routes>
      <Route
        path=""
        element={(<DashboardLayout><CardList /></DashboardLayout>)}
      />
      <Route
        path="sentCard"
        element={(<SentCardList />)}
      />
      <Route
        path="details/:id"
        element={(<CardDetails />)}
      />
      <Route
        path="create/type"
        element={(<CardType />)}
      />
      <Route
        path="create/info"
        element={(<CardInfo />)}
      />
      <Route
        path="create/address"
        element={(<CardAddress />)}
      />
      <Route
        path="create/billingaddress"
        element={(<CardBillingAddress />)}
      />
      <Route
        path="create/success"
        element={(<CardCreated />)}
      />
      <Route
        path="edit/info"
        element={(<EditCardInfo />)}
      />
      <Route
        path="atm/info"
        element={(<ATMInfo />)}
      />
      <Route
        path="atm/pin"
        element={(<AtmPin />)}
      />
      <Route
        path="atm/list"
        element={(<AtmList />)}
      />

      <Route
        path="activate"
        element={(<CardActivation />)}
      />

      <Route
        path="activated"
        element={(<CardActivated />)}
      />
    </Routes>
  );
}

export default AoRouter;
